<template>
    <div>
        <div class="bg">
            <div class="logo"></div>
        </div>
        <grid-cell :pages="pages"></grid-cell>
    </div>
</template>

<script>
import GridCell from '../../components/GridCell';
import localjson from '../../util/localjson';

export default {
    name: 'notice',
    components: {
        GridCell,
    },
    data() {
        return {
            pages:[
                { index:1 ,title: '公告通知', image: 'notice/ggtz.png', path: '/notice/ggtz', isValid: true, msgNumber: 2 },
                { index:2 ,title: '新闻动态', image: 'notice/xwdt.png', path: '/notice/xwdt', isValid: false },
                { index:3 ,title: '会议通知', image: 'notice/hytz.png', path: '/notice/hytz', isValid: false },
                { index:4 ,title: '通告警告', image: 'notice/tgjg.png', path: '/notice/tgjg', isValid: true },
                { index:5 ,title: '安全提醒', image: 'notice/aqtx.png', path: '/notice/aqtx', isValid: false },
                { index:6 ,title: '管制信息', image: 'notice/gzxx.png', path: '/notice/gzxx', isValid: false },
                { index:7 ,title: '调度通知', image: 'notice/ddtz.png', path: '/notice/ddtz', isValid: false },
                { index:8 ,title: '海区情况', image: 'notice/hqqk.png', path: '/notice/hqqk', isValid: false },
                { index:9 ,title: '其他通知', image: 'notice/qttz.png', path: '/notice/qttz', isValid: false },
            ],
        };
    },
    activated() {
        const currpage = {text: '公告通知', route: '/notice'};
        localjson.setCurrentPage(currpage);
        this.$store.commit('setCurrentPage', currpage);

        this.$store.commit('setAppHeader', {
            show: true,
            title: '公告通知',
            showMenu: true,
            showBack: false,
            actions: [
            ]
        });
        this.$store.commit('setEnableSwipeOut', true);
    },
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom

.bg
    background url('~/static/img/notice/bg.jpg') no-repeat center center
    margin 0 auto
    width 100%
    height 180px

    .logo
        margin 0 auto
        background url('~/static/img/notice/logo.png')
        background-size 100px 100px
        background-repeat no-repeat
        background-position center bottom
        width 100px
        height 130px
</style>



